const setOpenGradableMode = (state, setState, mode) => {
	const instance = state.activityInstance;
	const currentAnswers = instance.getTeacherFeedback();

	// modify the answers here to create an audio url if necessary
	currentAnswers.answers.forEach((answer) => {
		if (answer.value?.user?.audioBase64) {
			delete answer.value.user.audioBase64;
			answer.value.user.audioUrl = './audio/student-recording.mp3';
		}

		if (answer.value?.teacher?.audioBase64) {
			delete answer.value.teacher.audioBase64;
			answer.value.teacher.audioUrl = './audio/teacher-recording.mp3';
		}
	});

	// change the open gradable mode of the activity (default, leave teacher feedback, review teacher feedback)
	instance.setOpenGradableMode(mode, currentAnswers);

	setState({
		openGradableFeedbackMode: mode
	});
};

export default setOpenGradableMode;
