const calculateTotalPoints = (state, activity) => {
	const { gradableType, totalPointsAvailable } = activity;

	if (gradableType === 'item-based') {
		if (state.activityInstance) {
			return state.activityInstance.getItemPointsAvailable(state.currentItemId);
		} else {
			return 0;
		}
	} else {
		return totalPointsAvailable;
	}
};

export default calculateTotalPoints;
