import { Switch, Match } from 'solid-js';

import { useActivity } from './providers/activityProvider.js';

import ClosedGradable from './gradableTypeTemplates/closedGradable';
import NonGradable from './gradableTypeTemplates/nonGradable';
import OpenNonGradable from './gradableTypeTemplates/openNonGradable';
import Story from './gradableTypeTemplates/story';
import OpenGradable from './gradableTypeTemplates/openGradable';
import ItemBased from './gradableTypeTemplates/itemBased';
import AnswerKey from './gradableTypeTemplates/answerKey';

// choose the template to use based on the gradable type
const ChooseTemplate = () => {
	const {
		activity: { gradableType },
		state,
		actions
	} = useActivity();

	const containerClass = () => {
		let derivedClasses = `activityInstance ${state.showingMarkedAnswers ? ' checkingAnswers ' : ''}`;

		derivedClasses += state.isLoaded ? ' activityLoaded ' : '';
		derivedClasses += state.showScoreCard ? ' showingScoreCard ' : '';
		derivedClasses +=
			state.showingMarkedAnswers && actions.isPerfectScore() ? ' perfectScore ' : '';

		// strip any excess spaces
		return derivedClasses.trim().replace(/\s+/g, ' ');
	};

	return (
		<section class="section" data-template={gradableType}>
			<div class={containerClass()} data-loaded={state.isLoaded}>
				<Switch fallback={<h1>Unknown Gradable Type: {gradableType}</h1>}>
					<Match when={gradableType === 'closed-gradable'}>
						<ClosedGradable />
					</Match>
					<Match when={gradableType === 'non-gradable'}>
						<NonGradable />
					</Match>
					<Match when={gradableType === 'open-non-gradable'}>
						<OpenNonGradable />
					</Match>
					<Match when={gradableType === 'story'}>
						<Story />
					</Match>
					<Match when={gradableType === 'open-gradable'}>
						<OpenGradable />
					</Match>
					<Match when={gradableType === 'item-based'}>
						<ItemBased />
					</Match>
					<Match when={gradableType === 'answer-key'}>
						<AnswerKey />
					</Match>
				</Switch>
			</div>
		</section>
	);
};

export default ChooseTemplate;
