import { useActivity } from '../../../providers/activityProvider';

const disableButton = () => {
	const { state } = useActivity();

	// this function (disableButton) is called the state properties used inside it are changed, the entire function is only called / rendered once !
	return state.automation
		? false
		: state.showingAnswers || state.showingMarkedAnswers || state.showingNextAnswer;
};

export default disableButton;
