// @flow

import { createSignal } from 'solid-js';

import { useActivity } from '../../providers/activityProvider';

import { FONT_SIZES, FONT_SIZE_MENU_CLASSES } from './constants';

const FontSizeControls = () => {
	const {
		actions: { changeFontSize }
	} = useActivity();

	// Create a signal to control the class to show/hide the menu
	const [toggleClass, setToggleClass] = createSignal(FONT_SIZE_MENU_CLASSES.hidden);

	// Function to toggle the font size menu on and off
	const toggleFontSizeMenu = () => {
		toggleClass() === FONT_SIZE_MENU_CLASSES.hidden
			? setToggleClass(FONT_SIZE_MENU_CLASSES.shown)
			: setToggleClass(FONT_SIZE_MENU_CLASSES.hidden);
	};

	return (
		<div class={`changeFontSize ${toggleClass()}`}>
			<button class="changeFontSizeMenuButton" onClick={() => toggleFontSizeMenu()}>
				Change font size
			</button>
			<div class="changeFontSizeMenu">
				<button
					class={`changeFontSizeButton ${FONT_SIZES.small}`}
					onClick={() => changeFontSize(FONT_SIZES.small)}
				>
					Small
				</button>
				<button
					class={`changeFontSizeButton ${FONT_SIZES.medium}`}
					onClick={() => changeFontSize(FONT_SIZES.medium)}
				>
					Medium
				</button>
				<button
					class={`changeFontSizeButton ${FONT_SIZES.large}`}
					onClick={() => changeFontSize(FONT_SIZES.large)}
				>
					Large
				</button>
				<button
					class={`changeFontSizeButton ${FONT_SIZES.extraLarge}`}
					onClick={() => changeFontSize(FONT_SIZES.extraLarge)}
				>
					Extra large
				</button>
			</div>
		</div>
	);
};

export default FontSizeControls;
