import { createContext, useContext } from 'solid-js';
import { createStore } from 'solid-js/store';

import actions from '../actions';

import { defaultState } from '../providers/states';

const ActivityContext = createContext();

/*
 *
 * Creates an ActivityProvider for every activity loaded in the player - each has it's own context (states & actions)
 *
 */
export const ActivityProvider = (props) => {
	// eslint-disable-next-line solid/reactivity
	const {
		openGradableFeedbackOption,
		currentItemId,
		automation,
		playerType,
		storyPlayingMode,
		isMonoRepo
	} =
		// eslint-disable-next-line solid/reactivity
		props.playerOptions;

	// create a solidjs store for states and actions
	const [state, setState] = createStore({
		...defaultState,
		// openGradableFeedbackMode is provided by the url / browser parameters when the page is loaded
		openGradableFeedbackMode: openGradableFeedbackOption,
		// itemId (currentItemId) can be provided by the url / browser parameters for item-based activities
		currentItemId,
		storyPlayingMode,
		automation,
		playerType,
		isMonoRepo
	});

	// eslint-disable-next-line solid/reactivity
	const activity = props.activity;

	// build the value for the context provider
	// - this entire object (including the actions) is now available wherever `useActivity` is called
	const activityWithStateAndActions = {
		state,
		setState,
		activity,
		actions: actions(state, setState, activity)
	};

	return (
		<ActivityContext.Provider value={activityWithStateAndActions}>
			{props.children}
		</ActivityContext.Provider>
	);
};

export function useActivity() {
	return useContext(ActivityContext);
}
