import { useActivity } from '../../../providers/activityProvider';
import disableButton from './disableButton';
import i18n from '../../../i18n/i18n';

const ShowAnswersButton = () => {
	const {
		actions: { showAnswers }
	} = useActivity();

	return (
		<button
			class="showAnswers answersButton"
			disabled={disableButton()}
			onClick={() => showAnswers()}
			aria-controls="contentContainer"
			aria-label={i18n.t('components.buttons.showAnswers.ariaLabel')}
		>
			{i18n.t('components.buttons.showAnswers.buttonLabel')}
		</button>
	);
};

export default ShowAnswersButton;
