import { useActivity } from '../../providers/activityProvider';

const showScoreCard = () => {
	const {
		state,
		activity: { gradableType }
	} = useActivity();

	if (gradableType === 'story' && state.storyPlayingMode === 'reading') {
		return false;
	}

	if (state.playerType === 'popup-player') {
		return true;
	}

	return !!state.showScoreCard;
};

export default showScoreCard;
