import { useActivity } from '../../../providers/activityProvider';

const disableButton = () => {
	const {
		state,
		activity: { gradableType, itemBasedItems }
	} = useActivity();

	if (gradableType !== 'item-based') {
		return true;
	}

	// only disable the button if on the last item in the item based activity
	return (
		itemBasedItems &&
		itemBasedItems.findIndex((item) => item.id === state.currentItemId) ===
			itemBasedItems.length - 1
	);
};

export default disableButton;
