import { For } from 'solid-js';

import { useActivity } from '../providers/activityProvider';

const OpenGradableFeedbackMode = () => {
	const {
		state,
		actions: { setOpenGradableMode }
	} = useActivity();

	// yet another contender for shared types / enums from an installable package of shared types
	const options = [
		{
			key: 'default',
			value: 'Default'
		},
		{
			key: 'leaveTeacherFeedback',
			value: 'Leave teacher feedback'
		},
		{
			key: 'reviewTeacherFeedback',
			value: 'Review teacher feedback'
		},
		{
			key: 'answerKeyMode',
			value: 'Answer Key Mode'
		}
	];

	return (
		<select
			name="openGradableFeedbackMode"
			id="openGradableFeedbackOptions"
			class="actControls viewerSingleButton openGradableFeedbackOptions"
			title="open-gradable feedback mode"
			onChange={(e) => {
				setOpenGradableMode(e.currentTarget.value);
			}}
		>
			<For each={options}>
				{(option) => (
					<option
						value={option.key}
						selected={state.openGradableFeedbackMode === option.key}
					>
						{option.value}
					</option>
				)}
			</For>
		</select>
	);
};

export default OpenGradableFeedbackMode;
