import { batch } from 'solid-js';
import { resetAnswersState } from '../providers/states';
import setOpenGradableMode from './setOpenGradableMode';
import updateScreenReaderAnnouncer from './updateScreenReaderAnnouncer';
import i18n from '../i18n/i18n';

const resetAnswers = (state, setState, activity) => {
	const instance = state.activityInstance;

	// reset the RCF instance
	instance.hideMarkedAnswers();
	instance.hideFeedback();
	instance.resetAnswers(state.currentItemId);
	instance.enable();

	// setOpenGradableMode and setState are called in a batch as they both update state - you don't have to use 'batch' but it's a good idea
	batch(() => {
		// reset open gradable mode (if open gradable activity !)
		if (activity.gradableType === 'open-gradable') {
			setOpenGradableMode(state, setState, 'default');
		}

		setState({
			...resetAnswersState
		});
	});

	updateScreenReaderAnnouncer(instance.activity, i18n.t('actions.resetAnswers.announcement'));
};

export default resetAnswers;
