import { useActivity } from '../../providers/activityProvider';
import i18n from '../../i18n/i18n';

const ScoreCard = () => {
	const {
		state,
		actions: { calculateTotalPoints }
	} = useActivity();

	// This is a work around to get browsers to include the text as a single string and not 3 separate strings which messes with screen readers (VoiceOver)
	const ariaLabel = `${i18n.t('components.scoreCard.score')}`;

	return (
		<div class="scoreCard playerScore">
			<label class="scoreLabel" aria-label={ariaLabel}>
				{`${state.score || 0} / ${calculateTotalPoints()}`}
			</label>
		</div>
	);
};

export default ScoreCard;
