import buildPlayerActivity from './buildPlayerActivity';

const fetchByPath = async (pathToLoad) => {
	const playlistResponse = await fetch(`../viewer/api.php/multiple-activities/${pathToLoad}`);
	const activitiesFromServer = await playlistResponse.json();

	// map them into our derived activities for use in the player
	return activitiesFromServer.map(buildPlayerActivity);
};

const fetchById = async (activityId) => {
	const activityResponse = await fetch(`../viewer/api.php/activity/${activityId}`);
	const activityFromServer = await activityResponse.json();

	return [buildPlayerActivity(activityFromServer)];
};

export { fetchByPath, fetchById };
