const isPerfectScore = (state, activity) => {
	const { gradableType, totalPointsAvailable } = activity;

	const totalPoints =
		gradableType === 'item-based'
			? state.activityInstance.getItemPointsAvailable(state.currentItemId)
			: totalPointsAvailable;

	return state.score === totalPoints;
};

export default isPerfectScore;
