import { useActivity } from '../../providers/activityProvider';

const showCheckAnswersButton = () => {
	const {
		state: { automation, storyState },
		activity: { gradableType }
	} = useActivity();

	// if automation tests running, always show the buttons
	if (automation) {
		return true;
	}

	switch (gradableType) {
		case 'closed-gradable':
		case 'item-based':
			return true;

		case 'story':
			return !!storyState?.hasQuestions;

		default:
			return false;
	}
};

export default showCheckAnswersButton;
