import { showAnswersState } from '../providers/states';
import updateScreenReaderAnnouncer from './updateScreenReaderAnnouncer';
import i18n from '../i18n/i18n';

const showAnswers = (state, setState, activity) => {
	const instance = state.activityInstance;

	// hide any marked answers
	instance.hideMarkedAnswers(state.currentItemId);
	// show the correct answers for any existing current item id (if item based)
	instance.showAnswers(activity.correctAnswers, false, state.currentItemId);
	// disable the activity
	if (activity.gradableType !== 'story') {
		instance.disable();
	}

	setState({
		...showAnswersState
	});

	// Announce that the answers have been shown to the user
	updateScreenReaderAnnouncer(instance.activity, `${i18n.t('actions.showAnswers.announcement')}`);
};

export default showAnswers;
