/*
	Takes an activity json structure from the php back end, and maps it into a structure we
	want to use in the multiple-activities player / popup player
*/
const buildPlayerActivity = (activityJson) => {
	const {
		id,
		firstItemId,
		gradable: gradableType,
		pointsAvailable,
		itemPointsAvailable,
		teacherPointsAvailable,
		htmlContent: html,
		css,
		css768,
		mq768,
		jsonContent
	} = activityJson;

	// filter out the rcfPlaceHolder answers
	const ignorePlaceholders = (answer) => !answer.rcfPlaceHolder;

	// build correctAnswers json for use in the player
	const correctAnswers = JSON.parse(jsonContent);
	correctAnswers.answers = correctAnswers.answers?.filter(ignorePlaceholders);

	// build list of items if item-based activity
	const itemBasedItems = gradableType === 'item-based' ? JSON.parse(activityJson.itemsToc) : null;

	// build and return our derived activity
	const derivedActivity = {
		id,
		html,
		css,
		css768,
		mq768,
		correctAnswers,
		firstItemId,
		itemBasedItems,
		gradableType,
		pointsAvailable: Number(pointsAvailable || 0),
		itemPointsAvailable: Number(itemPointsAvailable || 0),
		teacherPointsAvailable: Number(teacherPointsAvailable || 0),
		totalPointsAvailable: Number(pointsAvailable || 0) + Number(teacherPointsAvailable || 0)
	};

	// this ends up in state.activity - is immutable
	return derivedActivity;
};

export default buildPlayerActivity;
