import { checkAnswersState } from '../providers/states';
import calculateTotalPoints from './calculateTotalPoints';
import updateScreenReaderAnnouncer from './updateScreenReaderAnnouncer';
import i18n from '../i18n/i18n';

const checkAnswers = (state, setState, activity) => {
	const instance = state.activityInstance;
	const userAnswers = instance.getUserAnswers(state.currentItemId);
	const markedAnswers = RCF.MarkingEngine.getMarkedAnswers(activity.correctAnswers, userAnswers);
	const totalPointsAvailable = calculateTotalPoints(state, activity);

	instance.showMarkedAnswers(markedAnswers, state.currentItemId);
	instance.showFeedback();

	if (activity.gradableType !== 'story') {
		instance.disable();
	}

	setState({
		...checkAnswersState,
		score: markedAnswers.score
	});

	// Announce the score to the user
	const scoreMessage = `${i18n.t('actions.checkAnswers.announcement', { userScore: markedAnswers.score, totalScore: totalPointsAvailable })}`;
	updateScreenReaderAnnouncer(instance.activity, scoreMessage);
};

export default checkAnswers;
