import { useActivity } from '../../providers/activityProvider';

const showShowAnswersButton = () => {
	const {
		state: { openGradableFeedbackMode, automation, storyState },
		activity: { gradableType }
	} = useActivity();

	// if automation tests running always return true
	if (automation) {
		return true;
	}

	switch (gradableType) {
		case 'answer-key':
		case 'closed-gradable':
		case 'item-based':
			return true;

		case 'open-gradable':
			return openGradableFeedbackMode === 'answerKeyMode';

		case 'story':
			return !!storyState?.hasQuestions;

		default:
			return false;
	}
};

export default showShowAnswersButton;
