import { tryAgainState } from '../providers/states';
import updateScreenReaderAnnouncer from './updateScreenReaderAnnouncer';
import i18n from '../i18n/i18n';

const tryAgain = (state, setState, activity) => {
	const instance = state.activityInstance;
	const userAnswers = instance.getUserAnswers(state.currentItemId);
	const markedAnswers = RCF.MarkingEngine.getMarkedAnswers(activity.correctAnswers, userAnswers);

	// hide any marked answers
	instance.hideMarkedAnswers();
	// hide any feedback
	instance.hideFeedback();
	// enable the activity
	instance.enable();
	// call the try again method on RCF to hide incorrect answers only
	instance.tryAgain(markedAnswers);

	setState({
		...tryAgainState
	});

	updateScreenReaderAnnouncer(instance.activity, i18n.t('actions.tryAgain.announcement'));
};

export default tryAgain;
