const showNextAnswer = (state, setState, activity) => {
	const instance = state.activityInstance;
	// call the showNextAnswer method on the activity instance
	const moreAnswers = instance.showNextAnswer(activity.correctAnswers, state.currentItemId);

	setState({
		showingNextAnswer: moreAnswers,
		moreAnswers: moreAnswers
	});
};

export default showNextAnswer;
