// @flow
import { render } from 'solid-js/web';
import { createSignal, createResource, For, Show } from 'solid-js';

import { ActivityProvider } from './providers/activityProvider';
import * as fetchActivities from './resources/activityResource';
import ChooseTemplate from './chooseTemplate';

const App = () => {
	// get playlist path parameter (this is the path /L0/U01/AS01 etc
	const urlParameters = new URLSearchParams(window.location.search);
	const runtimeLanguage = urlParameters.get('runtimeLanguage') || 'en';

	// create the reactive 'playlist' variable
	const [playlist /*, setPlaylist */] = createSignal(urlParameters.get('path') || 'L10/');

	// initialise RCF !
	RCF.Application.initialize({
		language: runtimeLanguage
	});

	// load our activities into a reactive array
	const [activities] = createResource(playlist, fetchActivities.fetchByPath);

	const playerOptions = {
		openGradableFeedbackOption: urlParameters.get('openGradableFeedbackOption') || 'default',
		storyPlayingMode: urlParameters.get('storyPlayingMode') || undefined,
		playerType: 'multiple-activity-player',
		isMonoRepo: urlParameters.get('rcfAsMonoRepo') === 'y'
	};

	// build the multiple activity player screen
	return (
		<div>
			<Show when={!activities.loading} fallback={<div>Loading</div>}>
				<For each={activities()}>
					{(activity) => (
						<ActivityProvider activity={activity} playerOptions={playerOptions}>
							<ChooseTemplate />
						</ActivityProvider>
					)}
				</For>
			</Show>
		</div>
	);
};

render(() => <App />, document.getElementById('app'));
