import { useActivity } from '../../../providers/activityProvider';
import disableButton from './disableButton';
import i18n from '../../../i18n/i18n';

const NextAnswerButton = () => {
	const {
		actions: { showNextAnswer }
	} = useActivity();

	return (
		<button
			class="nextAnswer answersButton"
			disabled={disableButton()}
			onClick={showNextAnswer}
			aria-controls="contentContainer"
			aria-label={i18n.t('components.buttons.nextAnswer.ariaLabel')}
		>
			{i18n.t('components.buttons.nextAnswer.buttonLabel')}
		</button>
	);
};

export default NextAnswerButton;
