import $ from 'jquery';

const updateScreenReaderAnnouncer = (activity, message) => {
	const $activity = activity.$activity;
	const $playerAriaLive = $activity.closest('body').find('.rcfPlayerAriaLive');
	if ($playerAriaLive.length > 0) {
		const $message = $(`<div>${message}</div>`);
		$playerAriaLive.empty();
		$playerAriaLive.append($message);
	}
};

export default updateScreenReaderAnnouncer;
