import { useActivity } from '../../providers/activityProvider';

const showTryAgainButton = () => {
	const {
		state,
		activity: { gradableType, pointsAvailable, teacherPointsAvailable }
	} = useActivity();

	const shouldShowTryAgainButton = () => {
		return (
			state.showingMarkedAnswers &&
			(state.score || 0) < Number(pointsAvailable || 0) + Number(teacherPointsAvailable || 0)
		);
	};

	switch (gradableType) {
		case 'story':
			return state.storyState?.hasQuestions && shouldShowTryAgainButton();
		default:
			return shouldShowTryAgainButton();
	}
};

export default showTryAgainButton;
