// @flow

import { FONT_SIZES } from '../components/fontSizeControls/constants';

const changeFontSize = (state, targetFontSize) => {
	const {
		activityInstance,
		activityInstance: {
			activity: { $activity }
		}
	} = state;

	// Remove any existing font classes
	$activity.removeClass([
		FONT_SIZES.small,
		FONT_SIZES.medium,
		FONT_SIZES.large,
		FONT_SIZES.extraLarge
	]);
	// Add new font class
	$activity.addClass(targetFontSize);
	// Trigger RCF resize api
	activityInstance.resize();
};

export default changeFontSize;
