import { onMount, onCleanup, Show } from 'solid-js';

import { useActivity } from '../../providers/activityProvider';
import onMountActivity from './onMountActivity';
import onCleanupActivity from './onCleanupActivity';
import FontSizeControls from '../fontSizeControls/fontSizeControls';

const Activity = () => {
	const {
		state,
		activity: { html, css, css768, mq768 } // the html for the activity, css used only in popup player / automation
	} = useActivity();

	// reference to the dom element used to hold the rcf content html element
	let activityContainerElement;

	// just like react - onMount is called the first time the component is rendered to the dom - so this is where we setup the RCF instance
	onMount(async () => {
		await onMountActivity(activityContainerElement);
	});

	// onCleanup is called when the component is unmounted - so this is where we clean up the RCF instance
	onCleanup(() => {
		onCleanupActivity(state);
	});

	const desktopStyles =
		css768?.length > 0 ? `@media screen and (min-width: ${mq768}) { ${css768} }` : '';

	// create the dom element and hold the ref for use in the 'onMount' callback
	return (
		<>
			<Show when={state.playerType === 'popup-player'}>
				<Show when={css?.length > 0}>
					<style id="mobStyleCSS">{css}</style>
				</Show>
				<Show when={css768?.length > 0}>
					<style id="desktopStyleCSS">{desktopStyles}</style>
				</Show>
			</Show>
			<Show when={state.isMonoRepo && !state.automation}>
				<FontSizeControls />
			</Show>
			<div ref={activityContainerElement} innerHTML={html} />
		</>
	);
};

export default Activity;
