/*
	Cannot use context here as it's already removed the state at that point !
*/
const onCleanupActivity = (state) => {
	try {
		state.activityInstance.closeView();
		state.activityInstance = null;
	} catch (ex) {
		/* eslint-disable no-console */
		console.error('Error closing RCF instance', ex);
	}
};

export default onCleanupActivity;
