import { batch } from 'solid-js';

import { useActivity } from '../../providers/activityProvider';

const onMountActivity = async (activityContainerElement) => {
	const {
		activity: { gradableType, firstItemId },
		state,
		setState,
		actions: { setOpenGradableMode }
	} = useActivity();

	if (gradableType === 'story') {
		// check if the window has jquery, if it does, bind to the story change page event
		if (window.$) {
			const $activityContainerElement = window.$(activityContainerElement);

			$activityContainerElement.on('rcf.story.change.page', (event, value) => {
				// when story changes page, update the story state in the activity state
				setState({
					storyState: {
						...value
					}
				});
			});
		}
	}

	// hydrate the html with the rcf runtime instance once the dom is built
	const activityInstance = await RCF.Content.newInstance(activityContainerElement);

	// get the current item id if item-based
	const useItemId =
		gradableType === 'item-based'
			? state.currentItemId
				? state.currentItemId
				: firstItemId
			: null;

	// store the rcf activity instance in the state against the activity for this player / set initial states
	batch(() => {
		setState({
			activityInstance,
			activityLoaded: true,
			currentItemId: useItemId,
			isLoaded: true
		});
		if (gradableType === 'open-gradable') {
			setOpenGradableMode(state.openGradableFeedbackMode);
		}
	});
};

export default onMountActivity;
