import { useActivity } from '../../providers/activityProvider';

const showNextAnswerButton = () => {
	const {
		state: { automation },
		activity: { gradableType }
	} = useActivity();

	// if automation tests running always return true
	if (automation) {
		return true;
	}

	switch (gradableType) {
		case 'closed-gradable':
		case 'open-non-gradable':
		case 'answer-key':
			return true;

		default:
			return false;
	}
};

export default showNextAnswerButton;
