import { previousItemState } from '../providers/states';

const gotoPreviousItem = (state, setState, activity) => {
	const instance = state.activityInstance;
	const itemBasedItems = activity.itemBasedItems;

	// get the index of the current item
	const currentItemIndex = itemBasedItems.findIndex((item) => item.id === state.currentItemId);

	if (currentItemIndex > 0) {
		// hide any marks
		instance.hideMarkedAnswers();

		// enable the activity instance
		instance.enable();

		// set the currentItemId to the previous item in the array (triggers the createEffect in the <ItemBased/> component !)
		setState({
			...previousItemState,
			currentItemId: itemBasedItems[currentItemIndex - 1].id
		});
	}
};

export default gotoPreviousItem;
