import { Show } from 'solid-js';

// selectors
import showTryAgainButton from './showTryAgainButton';
import showOpenGradableControls from './showOpenGradableControls';
import showShowAnswersButton from './showShowAnswersButton';
import showCheckAnswersButton from './showCheckAnswersButton';
import showNextAnswerButton from './showNextAnswerButton';
import showItemBasedButtons from './showItemBasedButtons';
import showScoreCard from './showScoreCard';
// controls
import CheckAnswersButton from '../buttons/checkAnswers/checkAnswersButton';
import ResetButton from '../buttons/reset/resetButton';
import ShowAnswersButton from '../buttons/showAnswers/showAnswersButton';
import TryAgainButton from '../buttons/tryAgain/tryAgainButton';
import NextAnswerButton from '../buttons/nextAnswer/nextAnswerButton';
import NextItemButton from '../buttons/nextItem/nextItemButton';
import PreviousItemButton from '../buttons/previousItem/previousItemButton';
import OpenGradableFeedbackMode from '../openGradableFeedbackMode';
import ScoreCard from '../scoreCard/scoreCard';
import ShowAnswersLabel from '../showAnswersLabel/showAnswersLabel';

import { useActivity } from '../../providers/activityProvider';

const ControlsBar = () => {
	const {
		state,
		activity: { gradableType }
	} = useActivity();

	return (
		<div class="activityControls playerToolbar">
			<Show when={state.playerType === 'popup-player'}>
				<div class="gradableTypeContainer">
					<label class="gradableTypeText">{gradableType}</label>
				</div>
			</Show>
			<div class="playerControls">
				<Show when={showOpenGradableControls()}>
					<OpenGradableFeedbackMode />
				</Show>
				<Show when={showCheckAnswersButton()}>
					<CheckAnswersButton />
				</Show>
				<Show when={showTryAgainButton()}>
					<TryAgainButton />
				</Show>
				<Show when={showShowAnswersButton() || showNextAnswerButton()}>
					<ShowAnswersLabel />
				</Show>
				<Show when={showShowAnswersButton()}>
					<ShowAnswersButton />
				</Show>
				<Show when={showNextAnswerButton()}>
					<NextAnswerButton />
				</Show>
				<Show when={showScoreCard()}>
					<ScoreCard />
				</Show>
				<Show when={showItemBasedButtons()}>
					<PreviousItemButton />
					<NextItemButton />
				</Show>
			</div>
			<div class="resetContainer">
				<ResetButton />
			</div>
		</div>
	);
};

export default ControlsBar;
