import { nextItemState } from '../providers/states';

const gotoNextItem = (state, setState, activity) => {
	const instance = state.activityInstance;
	const itemBasedItems = activity.itemBasedItems;

	// get the index of the current item
	const currentItemIndex = itemBasedItems.findIndex((item) => item.id === state.currentItemId);

	if (currentItemIndex > -1 && currentItemIndex < itemBasedItems.length - 1) {
		// hide any marks
		instance.hideMarkedAnswers();

		// enable the activity instance
		instance.enable();

		// set the currentItemId to the next item in the array (triggers the createEffect in the <ItemBased/> component !)
		setState({
			...nextItemState,
			currentItemId: itemBasedItems[currentItemIndex + 1].id
		});
	}
};

export default gotoNextItem;
