import { createEffect } from 'solid-js';

import Activity from '../components/activity/activity';
import ControlsBar from '../components/controlsBar/controlsBar';

import { useActivity } from '../providers/activityProvider';

const Story = () => {
	const { state } = useActivity();

	// we want to set any supplied story playing mode as soon as the activity instance is available
	createEffect(() => {
		const instance = state.activityInstance;
		if (instance && state.storyPlayingMode) {
			instance.setStoryPlayingMode(state.storyPlayingMode);
		}
	});

	return (
		<>
			<Activity />
			<ControlsBar />
		</>
	);
};

export default Story;
