import checkAnswers from './checkAnswers';
import resetAnswers from './resetAnswers';
import setOpenGradableMode from './setOpenGradableMode';
import showAnswers from './showAnswers';
import showNextAnswer from './showNextAnswer';
import tryAgain from './tryAgain';
import gotoNextItem from './gotoNextItem';
import gotoPreviousItem from './gotoPreviousItem';
import calculateTotalPoints from './calculateTotalPoints';
import isPerfectScore from './isPerfectScore';
import changeFontSize from './changeFontSize';

// - These actions are provided in the state supplied to each activity in the player
// - They are used to change the state of the player (and therefore the RCF activity itself)
const actions = (state, setState, activity) => {
	return {
		calculateTotalPoints: () => calculateTotalPoints(state, activity),
		isPerfectScore: () => isPerfectScore(state, activity),
		showAnswers: () => showAnswers(state, setState, activity),
		checkAnswers: () => checkAnswers(state, setState, activity),
		resetAnswers: () => resetAnswers(state, setState, activity),
		showNextAnswer: () => showNextAnswer(state, setState, activity),
		tryAgain: () => tryAgain(state, setState, activity),
		setOpenGradableMode: (mode) => setOpenGradableMode(state, setState, mode),
		gotoNextItem: () => gotoNextItem(state, setState, activity),
		gotoPreviousItem: () => gotoPreviousItem(state, setState, activity),
		changeFontSize: (targetFontSize) => changeFontSize(state, targetFontSize)
	};
};

export default actions;
